import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { LoginPage } from '../pages/login/login';
import { CuerdaPage } from '../pages/cuerda/cuerda';
import { PercucionPage } from '../pages/percucion/percucion';
import { PrincipalPage } from '../pages/principal/principal';
import { VientoPage } from '../pages/viento/viento';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { FIREBASE_CREDENTIALS } from './firebase.credentials';

import { NativeAudio } from '@ionic-native/native-audio';

import { ToastController } from 'ionic-angular';

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    PrincipalPage,
    PercucionPage,
    CuerdaPage,
    VientoPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/viento/viento.module#VientoPageModule', name: 'VientoPage', segment: 'viento', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/percucion/percucion.module#PercucionPageModule', name: 'PercucionPage', segment: 'percucion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cuerda/cuerda.module#CuerdaPageModule', name: 'CuerdaPage', segment: 'cuerda', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/principal/principal.module#PrincipalPageModule', name: 'PrincipalPage', segment: 'principal', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(FIREBASE_CREDENTIALS),
    AngularFireDatabaseModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    PrincipalPage,
    PercucionPage,
    CuerdaPage,
    VientoPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    NativeAudio,
    ToastController,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



